body {
  margin: 0px;
  height: 100vh;
}

.center {
  display: flex;
  justify-content: center;
  align-items: center;
}

.left-side-tab {
  width: 60%;
  height: 100vh;
  display: inline-block;
  display: flex;
  flex-direction: column;
  margin: 0px;

  .absolute-city-div {
    position: absolute;
    left: 5%;
    top: 7%;
    color: white;
  }

  .left-top-side {
    width: 100%;
    height: 70%;
    display: flex;
    justify-content: flex-end;

    .form-input {
      margin-top: 30px;
      margin-right: 30px;

      .city-not-found {
        margin-top: 10px;
        color: rgb(243, 24, 24);
        font-weight: 500;
        font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
      }

      input {
        width: 175px;
        height: auto;
        padding: 10px;
        background-color: rgba(120, 120, 120, 0.5);
        color: white;
        border: none;
        border-radius: 10px;
        outline: none;
        font-size: 15px;

        &::placeholder {
          color: rgba(255, 255, 255, 0.5);
        }
      }
    }
  }

  .left-bottom-side {
    width: 100%;
    height: 30%;

    .left-bottom-side-container {
      width: 80%;
      margin-left: 20%;
      display: flex;
      flex-direction: row;
    }

    .actual-temp {
      font-size: 45px;
      font-family: Arial, Helvetica, sans-serif;
      font-weight: 500;
      color: white;
    }

    .long-div {
      width: 2px;
      margin: 0px 7px;
      height: 45px;
      background-color: rgba(255, 255, 255, 0.7);
    }

    .feels-like {
      font-size: 20px;
      color: rgba(255, 255, 255, 1);
      @extend .center;
    }
  }
}

.right-side-tab {
  width: 40%;
  height: 100vh;
  display: inline-block;
  background-color: rgba(22, 49, 114, 0.6);
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;

  .box-container-right-side {
    width: 90%;
    height: 20%;
    @extend .center;
    flex-direction: column;
    //background-color: rgba(96, 122, 182, 0.6);
    
    .day-description {
      font-size: 25px;
      font-weight: 100;
      font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
      color: rgba(255, 255, 255, 0.7)
    }

    .high-temp {
      width: 40%;
      display: inline-block;
      text-align: center;
      font-size: 21px;
      font-weight: 100;
      font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
      color:rgba(255, 255, 255, 0.7)
    }

    .low-temp {
      width: 40%;
      display: inline-block;
      text-align: center;
      font-size: 21px;
      font-weight: 100;
      font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
      color:rgba(255, 255, 255, 0.7);
    }

    .pressure-title {
      width: 40%;
      display: inline-block;
      text-align: center;
      font-size: 21px;
      font-weight: 100;
      font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
      color:rgba(255, 255, 255, 0.7);
    }

    .pressure {
      width: 40%;
      display: inline-block;
      text-align: center;
      font-size: 21px;
      font-weight: 100;
      font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
      color:rgba(255, 255, 255, 0.7);
    }

    .button-box-container {
      width: 90%;
      height: 100%;
      display: flex;
      justify-content: space-around;
      align-items: center;
      flex-direction: row;

      button {
        width: 20%;
        height: 75px;
        background-color: rgba(255, 255, 255, 0.2);
        color: white;
        border: none;
        border-radius: 5px;
        outline: none;
        cursor: pointer;
      }
    }
  }
}
